import React, { Component } from 'react'

const soundBank1 =
{
    q: {
        keypad: 'q',
        soundName: 'Heater-1',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3'
    },

    w: {
        keypad: 'w',
        soundName: 'Heater-2',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3'
    },

    e: {
        keypad: 'e',
        soundName: 'Heater-3',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3'
    },

    a: {
        keypad: 'a',
        soundName: 'Heater-4',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3'
    },

    s: {
        keypad: 's',
        soundName: 'Clap',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3'
    },

    d: {
        keypad: 'd',
        soundName: 'Open HH',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3'
    },

    z: {
        keypad: 'z',
        soundName: 'Kick-n-Hat',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3'
    },

    x: {
        keypad: 'x',
        soundName: 'Kick',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3'
    },

    c: {
        keypad: 'c',
        soundName: 'Closed HH',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3'
    },
}

const soundBank2 =
{
    q: {
        keypad: 'q',
        soundName: 'Chord-1',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3'
    },

    w: {
        keypad: 'w',
        soundName: 'Chord-2',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3'
    },

    e: {
        keypad: 'e',
        soundName: 'Chord-3',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3'
    },

    a: {
        keypad: 'a',
        soundName: 'Shaker',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3'
    },

    s: {
        keypad: 's',
        soundName: 'Open-HH',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3'
    },

    d: {
        keypad: 'd',
        soundName: 'Closed-HH',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3'
    },

    z: {
        keypad: 'z',
        soundName: 'Punchy-Kick',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3'
    },

    x: {
        keypad: 'x',
        soundName: 'Side-Stick',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3'
    },

    c: {
        keypad: 'c',
        soundName: 'Snare',
        src: 'https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3'
    },
}



export default class Drum extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bank: soundBank1,
            pwr: false,
            currentSound: ''
        }
        this.playSound = this.playSound.bind(this)
        this.changeBank = this.changeBank.bind(this)
        this.powerOn = this.powerOn.bind(this)
    }



    changeBank(event) {
        if (this.state.pwr === true) {
            if (event.target.id === 'bank1') {
                this.setState({
                    bank: soundBank1,

                }, () => console.log(this.state.bank))
            } else if (event.target.id === 'bank2') {
                this.setState({
                    bank: soundBank2
                }, () => console.log(this.state.bank))
            } else {
                alert('something went wrong')
            }
            this.refs.audioq.load();
            this.refs.audiow.load();
            this.refs.audioe.load();
            this.refs.audioa.load();
            this.refs.audios.load();
            this.refs.audiod.load();
            this.refs.audioz.load();
            this.refs.audiox.load();
            this.refs.audioc.load();
        }
    }

    playSound(event, key) {
        if (this.state.pwr === true) {
            if (event !== null) {
                key = event.target.id
            }
            this.setState({
                currentSound: this.state.bank[key].soundName
            })
            const sound = document.getElementById(key).getElementsByTagName('audio')[0]
            sound.currentTime = 0
            sound.play()
        }
    }

    powerOn() {
        const btnList = document.getElementsByClassName('btn')
        const btnArray = [...btnList]
        if (this.state.pwr === true) {
            this.setState({
                pwr: false,
                currentSound: ''
            })
            btnArray.forEach(element => { element.className = element.className.slice(0, element.className.indexOf('glow')) })
            setTimeout(() => document.getElementById('display').className = '', 500)


        } else {
            this.setState({
                pwr: true
            })
            btnArray.forEach(element => { element.className = element.className + ' glow' })
            setTimeout(() => document.getElementById('display').className = 'display-glow', 500)
        }
    }


    componentDidMount() {
        document.addEventListener('keypress', (event) => {
            const reg = /[q,w,e,a,s,d,z,x,c]/
            if (reg.test(event.key) && this.state.pwr === true) {
                this.playSound(null, event.key)
                document.getElementById(event.key).className = 'active drum-pad btn btn-secondary title glow'
                setTimeout(() => document.getElementById(event.key).className = 'drum-pad btn btn-secondary title glow', 200)
            }
        })
    }



    render() {
        return (
            <div className="card glow" >
                <div className="card-body">
                    <div id='display'>{this.state.currentSound}</div>
                    <div className='d-flex justify-content-center'>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='q'>
                            <audio src={this.state.bank.q.src} className='clip' id='Q' ref='audioq'>
                                Your browser does not support the audio element.
                        </audio>
                            Q
                    </div>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='w'>
                            <audio src={this.state.bank.w.src} className='clip' id='W' ref='audiow'>
                                Your browser does not support the audio element.
                        </audio>
                            W
                    </div>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='e'>
                            <audio src={this.state.bank.e.src} className='clip' id='E' ref='audioe'>
                                Your browser does not support the audio element.
                        </audio>
                            E
                    </div>
                    </div>
                    <div className='d-flex justify-content-center'>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='a'>
                            <audio src={this.state.bank.a.src} className='clip' id='A' ref='audioa'>
                                Your browser does not support the audio element.
                        </audio>
                            A
                    </div>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='s'>
                            <audio src={this.state.bank.s.src} className='clip' id='S' ref='audios'>
                                Your browser does not support the audio element.
                        </audio>
                            S
                    </div>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='d'>
                            <audio src={this.state.bank.d.src} className='clip' id='D' ref='audiod'>
                                Your browser does not support the audio element.
                        </audio>
                            D
                    </div>
                    </div>
                    <div className='d-flex justify-content-center'>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='z'>
                            <audio src={this.state.bank.z.src} className='clip' id='Z' ref='audioz'>
                                {this.state.bank.src}
                            </audio>
                            Z
                    </div>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='x'>
                            <audio src={this.state.bank.x.src} className='clip' id='X' ref='audiox'>
                                Your browser does not support the audio element.
                        </audio>
                            X
                    </div>
                        <div onClick={this.playSound} className='drum-pad btn btn-secondary title' id='c'>
                            <audio src={this.state.bank.c.src} className='clip' id='C' ref='audioc'>
                                Your browser does not support the audio element.
                        </audio>
                            C
                    </div>
                    </div>
                    <div className='d-flex justify-content-center'>
                        <div className="ml-1  btn-group btn-group-toggle" data-toggle="buttons">
                            <label onClick={this.changeBank} id='bank1' className="mr-0 bank-pad btn btn-secondary ">
                                <input type="radio" name="options" autoComplete="off" /> Bank 1
                        </label>
                            <label onClick={this.changeBank} id="bank2" className="bank-pad btn btn-secondary">
                                <input type="radio" name="options" autoComplete="off" /> Bank 2
                        </label>
                        </div>
                        <div onClick={this.powerOn} className='ml-2 bank-pad title drumbtn btn btn-secondary' id='x'>PWR</div>
                    </div>
                </div>
            </div>
        );
    }
}